}

.image-1,
.image-2,
.image-3 {
  opacity: 1;
}
.image-1.active,
.image-2.active,
.image-3.active {
  -webkit-animation: fadeIn 1000ms ease forwards;
  animation: fadeIn 1000ms ease forwards;
  opacity: 1;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

